.background {
  height: 664px;
  background: linear-gradient(120deg, #ffb8ca, #771e32) 0 0 / 300% 300%;
  animation: cambiar 7s infinite;
  position: relative;
}

@keyframes cambiar {
  0% {
    background-position: 0;
  }

  50% {
    background-position: 100% 70%;
  }

  100% {
    background-position: 0%;
