  * {
    
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .background {
    
    height: 664px;
    position: relative;
    background: linear-gradient(120deg, #FFB8CA, rgb(119, 30, 50));
    background-size: 300% 300%;
    -webkit-animation: cambiar 7s infinite;
            animation: cambiar 7s infinite;
  }
  @-webkit-keyframes cambiar {
    0% {
      background-position: 0 50%;
    }
    50% {
      background-position: 100% 70%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  @keyframes cambiar {
    0% {
      background-position: 0 50%;
    }
    50% {
      background-position: 100% 70%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .burbujas-rigth {
    opacity: 0.4;
  }
  .burbujas-rigth__burbuja {
    border-radius: 50px;
    bottom: 0;
    position: absolute;
    -webkit-animation: bubblesr 2s normal infinite;
            animation: bubblesr 2s normal infinite;
  }
  .burbujas-rigth__burbuja:nth-child(1) {
    width: 100px;
    height: 100px;
    left: 20%;
    -webkit-animation-duration: 3.5s;
            animation-duration: 3.5s;
    background-color: #FFFFFF;
  }
  .burbujas-rigth__burbuja:nth-child(2) {
    width: 100px;
    height: 100px;
    left: 80%;
    position: absolute;
    -webkit-animation-duration: 6s;
            animation-duration: 6s;
    background-color: #FFFFFF;
  }
  @-webkit-keyframes bubblesr {
    0% {
      transform: translateX(0%);
    }
    15% {
      transform: translateX(0px);
    }
    40% {
      transform: translateX(110px);
    }
    95% {
      transform: translateX(90px);
    }
    100% {
      bottom: 100vh;
      transform: translateX(90px);
    }
  }
  @keyframes bubblesr {
    0% {
      transform: translateX(0%);
    }
    15% {
      transform: translateX(0px);
    }
    40% {
      transform: translateX(110px);
    }
    95% {
      transform: translateX(90px);
    }
    100% {
      bottom: 100vh;
      transform: translateX(90px);
    }
  }
  
