  /* modal */
  .modal {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(255, 238, 250);
    background-color: rgba(255, 144, 172, 0.4);
  }
  .modal:target {
    display: table;
    position: absolute;
    display: flex;
    justify-content: center;
    
  }
  .popup {
    margin-top: 5px;
    margin-bottom: 5px;
    width: 47%;
    height: 90%;
    display: flex;
    justify-content: center;  
    &__intcontent{
      width: 80%;
      height: 100%;
      border-radius: 5px;
      border: 4px solid #D41443;
      background-color: #FFB8CA;
      box-shadow: 0px 0px 5px 0 px #D41443;   
    }
    &__close{
      text-decoration: none;
      float: right;
      margin-top: 10px;
      margin-right: 10px;
      font-family: "Titan One", cursive;
      color: #D41443;
      font-size: 18px;
      font-weight: bold;
    } 
    &__h2{
      font-family: "Titan One", cursive;
      font-size: 48px;
      line-height: 43.51px;
      text-align: center;
      color: #D41443;
    }
    &__intcontent--imgs{
      height: 20%;
    display: flex;
    justify-content: space-around;
    }
    &__intcontent--img {
      margin-top: -38px;
    }
    &__intcontent--how {
      display: flex;
      justify-content: space-around;
      padding-bottom: 5px;
    }
    &__intcontent--input {
      border: 1px solid #D41443;
      border-radius: 5px;
      height: 20px;
      text-align: center;
    }
    &__intcontent--input::placeholder {
      font-family: "DM Sans", sans-serif;
      color: #7b7979;
      text-align: center;
      font-size: 16px;
    }
    &__intcontent--nombre {
      padding-left: 135px;
      padding-bottom: 5px;
    }
    &__intcontent--text {
      display: block;
      font-family: "DM Sans", sans-serif;
      color: #D41443;
      font-size: 18px;
    }
    &__intcontent--inputext {
      border: 1px solid #D41443;
      border-radius: 7px;
      height: 25px;
      padding-left: 5px;
    }
    &__intcontent--inputext::placeholder {
      font-family: "DM Sans", sans-serif;
      color: #7b7979;
      padding-left: 10px;
      font-size: 16px;
    }
    &__intcontent--inputextarea {
      margin-top: 10px;
      border: 1px solid #D41443;
      border-radius: 7px;
      height: 120px;
      font-family: "DM Sans", sans-serif;
      color: #7b7979;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 5%;
      text-align: center;
      padding-top: 15px;
      width: 223px;
      padding-left: 5px;
      padding-right: 5px;
    }
    &__intcontent--boton {
      margin-top: 5px;
      text-align: center;
    }
    &__button {
      font-family: "DM Sans", sans-serif;
      color: #D41443;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 5%;
      width: 159px;
      height: 44px;
      border-radius: 22px;
      border: none;
    }
    &__button:hover {
      transform: translateY(-5px);
    }
  }
